import { component } from 'picoapp';

class SizeGuideTrigger {
  constructor(el, ctx) {
    this.init(el)
  }

  init(el) {
    this.el = el;
    this.KiwiSizingChart = document.querySelector('#KiwiSizingChart');
    this.SizeGuideTrigger = document.querySelector('[data-size-guide-trigger]');
    this.el.style.display = 'none';
    this.bindEvents(this.el,this.SizeGuideTrigger);
  }

  bindEvents(el, sizeGuideTrigger ){
    
    const config = { childList: true, subtree: true };

    const callback = function(mutationsList, observer) {
      for(const mutation of mutationsList) {
        if (mutation.type === 'childList' && mutation.addedNodes && mutation.addedNodes[0].classList.contains('sizing-chart-modal-link')) {
          el.style.display = 'flex';
          const sizeGuideBtn = document.querySelector('.sizing-chart-modal-link');
          sizeGuideTrigger.addEventListener('click', (e) => {
            e.preventDefault();
            if(sizeGuideBtn){
              sizeGuideBtn.click();
            }
          })

          observer.disconnect();
        }
      }
    };

    const observer = new MutationObserver(callback);

    observer.observe(this.KiwiSizingChart, config);
  }
}

export default component((node, ctx) => {
  new SizeGuideTrigger(node, ctx);
});